
.filterList {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 502px;
  padding: 0;
  margin: 0;
}

.item {
  height: 50px;
  border: 1px solid #DFE5EC;
  background-color: white;
  font-size: 12px;
  letter-spacing: 0.5px;  
  flex-grow: 1;
  display: flex;
  justify-content: center;

  &:nth-child(1) {
      border-radius: 5px 0 0 5px;
  }

  &:nth-child(2) {
    @media (max-width: 400px) {
      border-radius: 0 5px 5px 0;
    }
  }

  &:nth-child(3) {
      border-radius: 0 5px 5px 0;

      @media (max-width: 400px) {
        border-radius: 5px;
      }
  }
}

.button {
  border: none; 
  outline: none; 
  background-color: white;
  padding: 15px;
  width: 100%;

  &:hover {
    background-color:  #F1FCFF;
    cursor: pointer;
  }

}

.active {
  background-color: #2196F3;
  color: white;

  &:hover {
    background-color: #1E88E5;
    cursor: pointer;
  }
}
